import React from 'react';
import Link from 'gatsby-link'

import Layout from '../components/Layout';
import Scroll from '../components/Scroll';

import config from '../../config';
import Footer from '../components/Footer';
import Header from '../components/Header';
import ScrollToTop from '../components/ScrollToTop';

import p1 from '../assets/images/portfolio-1.jpg';
import p2 from '../assets/images/portfolio-2.jpg';
import p3 from '../assets/images/portfolio-3.jpg';
import p4 from '../assets/images/portfolio-4.jpg';

const IndexPage = () => (
  <Layout>
    <Header />

    <header className="masthead d-flex">
      <div className="container text-center my-auto">
        <h1 className="mb-1" style={{color:"white"}}>{config.heading}</h1>
        <h3 className="mb-5">
          <em style={{color:"white"}}>{config.subHeading}</em>
        </h3>
        <Scroll type="id" element="about" offset={100}>
          <a className="btn btn-primary btn-xl" href="#about">
            Sobre
          </a>
        </Scroll>
      </div>
      <div className="overlay"></div>
    </header>

    <section className="content-section bg-light" id="about">
      <div className="container text-center">
        <div className="row">
          <div className="col-lg-10 mx-auto">
            <h2>
              PGFLEX comercializando Mangueiras e Terminais com qualidade!
            </h2>
            <p className="lead mb-5">
              This theme features a flexible, UX friendly sidebar menu and stock
              photos from our friends
            </p>
            <Scroll type="id" element="services" offset={100}>
              <a className="btn btn-dark btn-xl" href="#services">
                O que oferecemos
              </a>
            </Scroll>
          </div>
        </div>
      </div>
    </section>

    <section
      className="content-section bg-primary text-white text-center"
      id="services"
    >
      <div className="container">
        <div className="content-section-heading">
          <h3 className="text-secondary mb-0">Serviços</h3>
          <h2 className="mb-5">O que oferecemos</h2>
        </div>
        <div className="row">
          <div className="col-lg-3 col-md-6 mb-5 mb-lg-0">
            <span className="service-icon rounded-circle mx-auto mb-3">
              <i className="icon-trophy"></i>
            </span>
            <h4>
              <strong>Qualidade</strong>
            </h4>
            <p className="text-faded mb-0">Produtos de qualidade!</p>
          </div>
          <div className="col-lg-3 col-md-6 mb-5 mb-lg-0">
            <span className="service-icon rounded-circle mx-auto mb-3">
              <i className="icon-credit-card"></i>
            </span>
            <h4>
              <strong>Preço</strong>
            </h4>
            <p className="text-faded mb-0">
              O melhor preço que você pode encontrar!
            </p>
          </div>
          <div className="col-lg-3 col-md-6 mb-5 mb-md-0">
            <span className="service-icon rounded-circle mx-auto mb-3">
              <i className="icon-people"></i>
            </span>
            <h4>
              <strong>Atendimento</strong>
            </h4>
            <p className="text-faded mb-0">
              Atendimento diferenciado!<p> </p> 
              <i className="fas fa-heart"></i>
            </p>
          </div>
          <div className="col-lg-3 col-md-6">
            <span className="service-icon rounded-circle mx-auto mb-3">
              <i className="icon-list"></i>
            </span>
            <h4>
              <strong>Variedade</strong>
            </h4>
            <p className="text-faded mb-0">Uma variedade de produtos de qualidade!</p>
          </div>
        </div>
      </div>
    </section>

    <section className="callout">
      <div className="container text-center">

      </div>
    </section>

    <section className="content-section" id="portfolio">
      <div className="container">
        <div className="content-section-heading text-center">
          <h3 className="text-secondary mb-0">Produtos</h3>
          <h2 className="mb-5">Alguns de nossos produtos</h2>
        </div>
        <div className="row no-gutters">
          <div className="col-lg-6">
            <a className="portfolio-item" href="/#">
              <span className="caption">
                <span className="caption-content">
                  <h2>Item 1</h2>
                  <p className="mb-0">
                  Mangueira hidráulica de super alta pressão de 4 espirais de aço, 4 espirais de aço de altíssima resistência, Conforme Norma ISO 3862 / Norma EN 856 tipo 4SH, aplicação: fluidos hidráulicos à base de petróleo, bitolas de 3/4″ a 2″.
                  </p>
                </span>
              </span>
              <img className="img-fluid" src={p1} alt="" />
            </a>
          </div>
          <div className="col-lg-6">
            <a className="portfolio-item" href="/#">
              <span className="caption">
                <span className="caption-content">
                  <h2>Item 2</h2>
                  <p className="mb-0">
                  Mangueira em borracha sintética, 4 espirais de aço, de altíssima resistência, Conforme Norma ISO 3862 / Norma EN 856 tipo 4SP, aplicação fluidos hidráulicos à base de petróleo, bitolas de 1/4″ a 2″.
                  </p>
                </span>
              </span>
              <img className="img-fluid" src={p2} alt="" />
            </a>
          </div>
          <div className="col-lg-6">
            <a className="portfolio-item" href="/#">
              <span className="caption">
                <span className="caption-content">
                  <h2>Item 3</h2>
                  <p className="mb-0">
                  Mangueira hidráulica termoplástica de alta pressão de 1 trama de poliester. Simples e germinadas, conforme norma ISO 3949 / norma EN 855 tipo R7 / norma SAER J 517 tipo SAE 100R7, resistente a óleo sem costura, aplicação em fluidos hidráulicos à base de petróleo, anti-estática e resistente a tintas, vernizes e solvente.
                  </p>
                </span>
              </span>
              <img className="img-fluid" src={p3} alt="" />
            </a>
          </div>
          <div className="col-lg-6">
            <a className="portfolio-item" href="/#">
              <span className="caption">
                <span className="caption-content">
                  <h2>Item 4</h2>
                  <p className="mb-0">
                  Mangueira hidráulica termoplática, NÃO CONDUTIVA, anti-estática, de alta pressão com uma trama de poliéster, aplicação em fluidos hidráulicos à base de petróleo.
                  </p>
                </span>
              </span>
              <img className="img-fluid" src={p4} alt="" />
            </a>
          </div>
        </div>
      </div>
    </section>

    <section className="content-section bg-primary text-white">
      <div className="container text-center">
        <h2 className="mb-4">Contato</h2>
        <a href="https://wa.me/5542988255603" className="btn btn-xl btn-light mr-4">
        Whatsapp! <i className="fab fa-whatsapp"></i>
          
        </a>
        
        <a href="mailto:pgflex@pgflex.com" className="btn btn-xl btn-dark">
          E-mail! <i className="fas fa-envelope-square"></i>
        </a>
        
      </div>
    </section>

     <section id="contact" className="map">
      <iframe
        width="100%"
        height="100%"
        frameborder="0"
        scrolling="no"
        marginheight="0"
        marginwidth="0"
        src="https://maps.google.com/maps?f=q&amp;source=s_q&amp;hl=en&amp;geocode=&amp;q=Twitter,+Inc.,+Market+Street,+San+Francisco,+CA&amp;aq=0&amp;oq=twitter&amp;sll=28.659344,-81.187888&amp;sspn=0.128789,0.264187&amp;ie=UTF8&amp;hq=Twitter,+Inc.,+Market+Street,+San+Francisco,+CA&amp;t=m&amp;z=15&amp;iwloc=A&amp;output=embed"
      ></iframe>
      <br />
      <small>
        <a href="https://maps.google.com/maps?f=q&amp;source=embed&amp;hl=en&amp;geocode=&amp;q=Twitter,+Inc.,+Market+Street,+San+Francisco,+CA&amp;aq=0&amp;oq=twitter&amp;sll=28.659344,-81.187888&amp;sspn=0.128789,0.264187&amp;ie=UTF8&amp;hq=Twitter,+Inc.,+Market+Street,+San+Francisco,+CA&amp;t=m&amp;z=15&amp;iwloc=A"></a>
      </small>
    </section> 

    <ScrollToTop />
    <Footer />
  </Layout>
);

export default IndexPage;
